import React, { Suspense, Component } from 'react'
import ImagePreloader from '../ImagePreloader/ImagePreloader';
import  './ParticlesJS.scss';
import userImg from '../img/profileb.png';

const Particles = React.lazy(() => import('react-particles-js'));

export class ParticlesJS extends Component {

    render() {

        return (
            <Suspense fallback={
                <div className="uk-section uk-text-center uk-section-default" style={{width: "100vw", height: "100vh", position: "fixed", zIndex: "998", top: "0", left: "0"}}>
                <div className="uk-container uk-container-xlarge uk-margin-xlarge-top">
                  <span data-uk-spinner="ratio: 8;" className="custom-color"></span>
                </div>
              </div>
            }>
                <div className="home-container">
                    <div className="uk-section uk-text-center">
                        <div className="uk-container uk-container-xlarge uk-margin-xlarge-top" >
                
                            <h1>Nicolás García Lira</h1>
                            <div className="uk-card uk-card-body uk-width-1@m">
                                <h2 className="uk-card-title">Ingeniero en sistemas computacionales</h2>
                                <ImagePreloader
                                    src={userImg}
                                    className="uk-border-rounded"
                                />
                            </div>
                            <div className="uk-position-bottom-center uk-overlay uk-margin-small-bottom uk-text-center">
                                <a className="uk-link-reset" href="#AboutMe" data-uk-icon="icon: arrow-down; ratio: 1.8">Más sobre mí</a>
                            </div>
                        </div>
                    </div>
                </div>
                <Particles className="particles-js" id="Home"
                params={{
                    "fps_limit": 90,
                    "particles": {
                        "number": {
                            "value": 30,
                            "density": {
                                "enable": true,
                                "value_area": 800
                            }
                        },
                        "line_linked": {
                            "enable": false
                        },
                        "move": {
                            "random": true,
                            "speed": 3,
                            "direction": "bottom",
                            "out_mode": "bounce",
                        },
                        "shape": {
                            "type": [
                                "image",
                            ],
                            "image": [
                                {
                                    "src": this.props.CsLogo,
                                    "height": 20,
                                    "width": 20
                                },
                                {
                                    "src": this.props.JavaLogo,
                                    "height": 25,
                                    "width": 25
                                },
                                {
                                    "src": this.props.JsLogo,
                                    "height": 25,
                                    "width": 25
                                },
                                {
                                    "src": this.props.SQLServerLogo,
                                    "height": 30,
                                    "width": 40
                                },
                                {
                                    "src": this.props.GitLogo,
                                    "height": 25,
                                    "width": 25
                                },
                            ]
                        },
                        "opacity": {
                            "anim": {
                                "enable": true,
                                "opacity_min": 0.4,
                                "speed": 1,
                                "sync": false
                            },
                            "value": 0.9
                        },
                        "size": {
                            "value": 30,
                            "random": true,
                            "anim": {
                                "enable": true,
                                "speed": 4,
                                "size_min": 10,
                                "sync": false
                            }
                        }
                    },
                    "retina_detect": false
                }}/>
            </Suspense>
        )
    }
}

export default ParticlesJS
